body {
  margin: 0;
  background: #ddd;
  min-height: 100vh;
  background-image: url("../img/bcg.jpg");
  background-size: cover;
  min-width: 1250px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

#wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 1246px;
  justify-content: space-between;
}

#wrapper div {
  margin: 0.1%;
}

#wrapper #header,
#wrapper #print {
  flex: 1 1 95%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  font-family: Arial, sans-serif;
}

#wrapper #header .lang_btn,
#wrapper #print .lang_btn {
  flex: 0 1 30%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#wrapper #header .lang_btn div,
#wrapper #print .lang_btn div {
  flex: 0 1 45%;
  background-color: white;
  color: orange;
  text-align: center;
  padding: 3px;
  cursor: pointer;
  font-size: 20px;
  border: 1px solid orange;
  margin: 0px;
}

#wrapper #header .lang_btn .rus,
#wrapper #print .lang_btn .rus {
  border-radius: 10px 0 0 10px;
}

#wrapper #header .lang_btn .eng,
#wrapper #print .lang_btn .eng {
  border-radius: 0 10px 10px 0;
}

#wrapper #header .lang_btn .active,
#wrapper #print .lang_btn .active {
  background-color: orange;
  color: white;
  cursor: auto;
}

#wrapper #header .createGame,
#wrapper #print .createGame,
#wrapper #header .printBtn,
#wrapper #print .printBtn {
  color: white;
  text-align: center;
  padding: 3px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
}

#wrapper #header .createGame:hover,
#wrapper #print .createGame:hover,
#wrapper #header .printBtn:hover,
#wrapper #print .printBtn:hover {
  transition: background 0.2s, color 0.2s;
  color: white;
}

#wrapper #header .createGame,
#wrapper #print .createGame {
  flex: 0 1 30%;
  background-color: #228694;
  border: 1px solid #228694;
}

#wrapper #header .createGame:hover,
#wrapper #print .createGame:hover {
  background-color: #43af43;
  border-color: #43af43;
}

#wrapper #header .printBtn,
#wrapper #print .printBtn {
  flex: 0 1 40%;
  border: 1px solid #6a6acc;
  background-color: #6a6acc;
  margin-top: 20px;
}

#wrapper #header .printBtn:hover,
#wrapper #print .printBtn:hover {
  background-color: #2172e6;
  border-color: #2172e6;
}

#wrapper #header {
  margin-bottom: 15px;
}

#wrapper #items {
  flex: 1 1 100%;
  display: flex;
  flex-wrap: wrap;
}

#wrapper #items .item {
  flex: 0 1 19%;
  min-width: 245px;
  height: 151px;
  background-image: url("../img/card.png");
  background-size: 100%;
  position: relative;
  font-family: Arial, sans-serif;
  font-weight: bold;
  box-shadow: 2px 2px 16px 1px #ab9d9d;
}

#wrapper #items .item .small-word {
  position: absolute;
  text-align: left;
  width: 134px;
  color: #827265;
  font-size: 19px;
  top: 43px;
  left: 29px;
  transform: scale(-1);
}

#wrapper #items .item .big-word {
  position: absolute;
  text-align: center;
  width: 196px;
  height: 41px;
  color: black;
  font-size: 23px;
  top: 81px;
  left: 24px;
}

@media print {
  #wrapper {
    width: 100% !important;
  }

  #header {
    display: none !important;
  }

  #print {
    display: none !important;
  }

  .item {
    box-shadow: none !important;
    border: 0.1px solid black !important;
  }

@page {
    size: landscape;
}
}